<template>
	<div class="logistics-info">
        <el-timeline direction="vertical" :active="1" class="steps" :space="80">
            <el-timeline-item
                    v-for="(item, index) in deliveryList"
                    :key="index"
                    :icon="item.icon"
                    :type="item.type"
                    :color="item.color"
                    :size="item.size"
                    :timestamp="item.time">
                <template v-if="item.hasOwnProperty('status')"> {{textType(item.status,item.delivery_name,item.delivery_num).text}} </template>
                <template v-else> {{item.context}} </template>
            </el-timeline-item>
        </el-timeline>
    </div>
</template>

<script>
	export default {
		name: "LogisticsInfo",
		data () {
		    return {

            }
        },
        props:{
            deliveryList:{
                type:Array,
                default: ()=>{
                    return [];
                }
            }
        },
        methods : {
		    textType(status,name,num){
                let x;
                switch (status) {
                    case 0:
                        x = {text: this.$L('你的订单已提交')};
                        break;
                    case 1:
                        x = {text: this.$L('等待商家配货')};
                        break;
                    case 2:
                        x = {text: this.$L('商家已配货')};
                        break;
                    case 3:
                        x = {text: this.$L('等待商家发货')};
                        break;
                    case 4:
                        x = {text: this.$L('商家已发货，') + name + num };
                        break;
                    case 5:
                        x = {text: this.$L('卖家已发货,等待快递员上门揽件')};
                        break;
                    case 6:
                        x = {text: this.$L('商品正在退货中')};
                        break;
                    case 7:
                        x = {text: this.$L('订单退货已完成')};
                        break;
                    default:
                        x = {text: this.$L('未知状态')};
                        break;
                }
                return x;
            },
        }

	}
</script>

<style lang="scss" scoped>
    .logistics-info{
        padding: 0 10px 0 10px;
        .steps{

        }
    }
</style>
