<template>
    <div class="bottom-btn" v-if="orderInfo">
        <el-button size="small" @click="backFunc">{{$L('返回')}}</el-button>
        <div v-if="orderInfo.orderType === 2" style="display: inline-block;">
            <!--拼购订单状态-->
            <el-button
                    v-if="orderInfo.hasOwnProperty('refundStatus') && orderInfo.refundStatus === 1"
                    @click="changeOrderType(orderInfo,'refund')"
                    class="item-btn"
                    size="small">{{$L('申请退款')}}</el-button>
            <el-button v-if="orderInfo.thisOrderStatus === 3 || orderInfo.thisOrderStatus === 4"
                       size="small"
                       class="item-btn"
                       @click="changeOrderType(orderInfo,'afterSaleInfo')">{{$L('售后详情')}}</el-button>
            <el-button
                    v-if="orderInfo.hasOwnProperty('pStatus') && orderInfo.pStatus === 2"
                    @click="changeOrderType(orderInfo,'pay')"
                    class="item-btn"
                    size="small">{{$L('立即付款')}}</el-button>
            <el-button v-if="orderInfo.hasOwnProperty('pStatus') && orderInfo.pStatus === 5"
                       type="primary"
                       size="small"
                       class="item-btn"
                       @click="changeOrderType(orderInfo,'finish')">{{$L('确认收货')}}</el-button>
            <el-button
                    v-if="orderInfo.hasOwnProperty('pStatus') && orderInfo.pStatus === 6"
                    @click="changeOrderType(orderInfo,'refund')"
                    class="item-btn"
                    size="small">{{$L('申请退款')}}</el-button>
            <el-button v-if="orderInfo.commentsStatus === 0"
                       size="small"
                       type="primary"
                       class="item-btn"
                       @click="changeOrderType(orderInfo,'evaluate')">{{$L('去评价')}}</el-button>
            <el-button type="text"
                       class="item-btn"
                       v-if="orderInfo.commentsStatus === 1"
                       @click="changeOrderType(orderInfo,'evaluateInfo')">{{ $L('查看评价') }}></el-button>
        </div>
        <div v-else style="display: inline-block;">
            <el-button
                    v-if="orderInfo.hasOwnProperty('refundStatus') && orderInfo.refundStatus === 1"
                    @click="changeOrderType(orderInfo,'refund')"
                    class="item-btn"
                    size="small">{{$L('申请退款')}}</el-button>
            <el-button type="text"
                       class="item-btn"
                       v-if="orderInfo.commentsStatus === 1"
                       @click="changeOrderType(orderInfo,'evaluateInfo')">{{ $L('查看评价') }}></el-button>
            <el-button v-if="orderInfo.thisOrderStatus === 3 || orderInfo.thisOrderStatus === 4"
                       size="small"
                       class="item-btn"
                       @click="changeOrderType(orderInfo,'afterSaleInfo')">{{$L('售后详情')}}</el-button>
            <el-button
                    v-if="orderInfo.thisOrderStatus < 0 || orderInfo.thisOrderStatus == 4 || orderInfo.thisOrderStatus == 5"
                    @click="changeOrderType(orderInfo,'del')"
                    class="item-btn"
                    size="small">{{$L(' 删除订单')}}</el-button>
            <el-button v-if="orderInfo.thisOrderStatus === 0"
                       size="small"
                       class="item-btn"
                       @click="changeOrderType(orderInfo,'cancel')">{{$L('取消订单')}}</el-button>
            <el-button v-if="orderInfo.commentsStatus === 0"
                       size="small"
                       type="primary"
                       class="item-btn"
                       @click="changeOrderType(orderInfo,'evaluate')">{{$L('去评价')}}</el-button>


            <el-button v-if="(orderInfo.thisOrderStatus === 0 || orderInfo.thisOrderStatus === 1) && orderInfo.payStatus >= 0" type="primary"
                       size="small"
                       class="item-btn"
                       @click="changeOrderType(orderInfo,'pay')">{{$L('立即付款')}}</el-button>

            <el-button v-if="orderInfo.thisOrderStatus === 2"
                       type="primary"
                       size="small"
                       class="item-btn"
                       @click="changeOrderType(orderInfo,'finish')">{{$L('确认收货')}}</el-button>
        </div>
    </div>
</template>

<script>
    import orderPayMobile from "../../views/mobile/orderChildren/orderPay";
    import orderPay from "../../views/web/orderChildren/orderPay.vue";
    export default {
        name: "btnStates",
        inject: ['reload'],
        components:{
            orderPay
        },
        props:{
            orderInfo:{
                type:Object,
                default:()=>{
                    return {};
                }
            }
        },
        methods:{
            changeOrderType(item, status) {
                switch (status) {
                    case 'del':
                        //删除订单
                        if (item.thisOrderStatus < 0 || item.thisOrderStatus == 4 || item.thisOrderStatus == 5) {
                            this.$confirm(this.$L('是否确认删除订单？'), this.$L('删除订单'), {
                                confirmButtonText: this.$L('立即删除'),
                                cancelButtonText: this.$L('我再想想'),
                                type: 'warning',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                this.showLoad(true);
                                this.$api('changeType', {
                                    orderId: item.orderId,
                                    orderType: 'del'
                                }, (reData) => {
                                    this.showLoad(false);
                                    if (reData && reData.flag === 1) {
                                        this.$message.success(this.$L('操作成功！'));
                                        //删除后返回列表
                                        this.$router.push({name:'orderList'});
                                    } else {
                                        console.log(reData);
                                        this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    }
                                }, (error) => {
                                    console.log(error);
                                    this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    this.showLoad(false);
                                });
                            }).catch(action => {

                            });
                        }
                        break;
                    case 'refund':
                        if (item.hasOwnProperty('refundStatus') && item.refundStatus === 1) {
                            //申请售后
                            this.$router.push({name: "orderService", params: {id: item.orderId}});
                            break;
                        }
                        break;
                    case 'pay':
                        //立即付款
                        if ((item.thisOrderStatus === 0 || item.thisOrderStatus === 1) && item.payStatus >= 0) {
                            this.$store.commit('setDialogObj', {
                                name: this.$L('订单支付'),
                                view: this.$isMobile()?orderPayMobile:orderPay,
                                width: '900px',
                                loading: true,
                                data:{
                                    orderId: item.orderId,
                                }
                            });
                        }
                        break;
                    case 'cancel':
                        //取消订单
                        if (item.thisOrderStatus === 0) {
                            this.$confirm(this.$L('确认取消订单吗？'), this.$L('取消订单'), {
                                confirmButtonText: this.$L('立即取消'),
                                cancelButtonText: this.$L('我再想想'),
                                type: 'warning',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                this.showLoad(true);
                                this.$api('changeType', {
                                    orderId: item.orderId,
                                    orderType: 'cancel'
                                }, (reData) => {
                                    this.showLoad(false);
                                    if (reData && reData.flag === 1) {
                                        this.$message.success(this.$L('操作成功！'));
                                        this.reloadData();
                                    } else {
                                        console.log(reData);
                                        this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    }
                                }, (error) => {
                                    console.log(error);
                                    this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    this.showLoad(false);
                                });
                            }).catch(action => {

                            });
                        }
                        break;
                    case 'finish':
                        //确认收货
                        if (item.thisOrderStatus === 2) {
                            this.$confirm(this.$L('是否确认收货？'), this.$L('确认收货'), {
                                confirmButtonText: this.$L('立即确认'),
                                cancelButtonText: this.$L('我再想想'),
                                type: 'warning',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                this.showLoad(true);
                                this.$api('changeType', {
                                    orderId: item.orderId,
                                    orderType: 'finish'
                                }, (reData) => {
                                    this.showLoad(false);
                                    if (reData && reData.flag === 1) {
                                        this.$message.success(this.$L('操作成功！'));
                                        this.reloadData();
                                    } else {
                                        console.log(reData);
                                        this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    }
                                }, (error) => {
                                    console.log(error);
                                    this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    this.showLoad(false);
                                });
                            }).catch(action => {

                            });
                        }
                        break;
                    case 'evaluate':
                        //去评价
                        if (item.commentsStatus === 0) {
                            this.$router.push({name: "orderComment", params: {id: item.orderId}});
                        }
                        break;
                    case 'afterSaleInfo':
                        //售后详情
                        if (item.thisOrderStatus === 3 || item.thisOrderStatus === 4) {
                            this.$router.push({name: "orderServiceInfo", params: {id: item.orderId}});
                        }
                        break;
                    case 'evaluateInfo':
                        //评价详情
                        if (item.commentsStatus === 1) {
                            this.$router.push({name: "checkComment", params: {id: item.orderId}});
                        }
                        break;
                }
            },
            backFunc() {
                this.$router.push({name:'orderList',params:{type:'all'}});
            },
            showLoad(type){
                this.$emit('showLoad',type);
                if(!type){
                    this.reload();
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    .bottom-btn {
        padding: 20px;
        background: #fff;
        border-top: 1px solid #eee;
        text-align: right;

        button {
            width: 100px;
            margin: 0 10px;
        }
    }
</style>