<template>
    <div class="prod-list" v-if="orderInfo!==null">
        <el-table
                :data="orderInfo.getProductInfo"
                :header-cell-style="{background:'#f0f1f5',padding:'10px',color:'#666'}"
                style="width: 100%" class="table">
            <el-table-column
                    :label="$L('产品信息')"
                    align="left"
                    min-width="200">
                <div slot-scope="scope" class="info">
                    <div class="list-main-left">
                        <img class="img" :src="scope.row.smallImg" alt="">
                    </div>
                    <div class="list-main-right">
                        <p class="orderInfo">{{scope.row.chrName}}</p>
                        <p v-html="scope.row.goodsSpec"></p>
                    </div>
                </div>
            </el-table-column>
            <el-table-column
                    :label="$L('价格')"
                    align="center">
                <div slot-scope="scope">
                    {{ scope.row.goodsPrice }}
                </div>
            </el-table-column>
            <el-table-column
                    :label="$L('规格')"
                    align="center">
                <div slot-scope="scope">
                    <p v-html="scope.row.goodsSpec?scope.row.goodsSpec:$L('无')"></p>
                </div>
            </el-table-column>
            <el-table-column
                    :label="$L('数量')"
                    align="center">
                <div slot-scope="scope">
                    {{ scope.row.goodsNumber}}
                </div>
            </el-table-column>
            <el-table-column
                    :label="$L('状态')"
                    align="center">
                <div slot-scope="scope">
                    {{ statusFunc(orderInfo.payStatus)}}
                </div>
            </el-table-column>
        </el-table>
        <div class="spell" v-if="orderInfo.orderType == 2 && orderInfo.remaining > 0">
            <div v-if="orderInfo.pStatus === 1">
                <div class="title"> {{$L('限时凑单：离结束还有')}} <span class="num">{{d}}</span> {{ $L('天')}} <span class="num">{{h}}</span> {{ $L('时')}} <span class="num">{{m}}</span> {{ $L('分')}} <span class="num">{{s}}</span> {{ $L('秒')}}</div>
                <ul class="ul">
                    <li class="li" v-for="item in orderInfo.exuserHead">
                        <img :src="item" alt="" class="img">
                    </li>
                    <li class="text">
                        {{orderInfo.peopleNum}}{{$L('人团：')}}<br>
                        {{ $L('还差')}}{{orderInfo.peopleNum - orderInfo.exuserHead.length}}{{$L('人成团')}}
                    </li>
                </ul>
            </div>
            <div v-if="orderInfo.pStatus === 2">
                {{$L('拼团已成功，赶紧去支付吧！')}}
            </div>
            <div v-if="orderInfo.pStatus === 5">
                {{$L('拼团已完成，卖家已经发货了！')}}
            </div>
            <div v-if="orderInfo.pStatus === 6">
                {{$L('拼购已完成，请确认收货咯！')}}
            </div>
        </div>
        <div class="prod-bottom">
            <div class="right-box">
                <ul class="ul">
                    <li class="li"><span>{{ $L('商品总额') }}:</span><span>{{ orderInfo.goodsAmount }}</span></li>
                    <li class="li"><span>{{ $L('店铺优惠') }}:</span><span>{{ orderInfo.discountMoney }}</span></li>
                    <li class="li"><span>{{ $L('包装费用') }}:</span><span>{{ orderInfo.packFee }}</span></li>
                    <li class="li"><span>{{ $L('运费') }}:</span><span>{{ orderInfo.deliveryFee }}</span></li>
                    <li class="li"><span>{{ $L('支付手续费') }}:</span><span>{{ orderInfo.payFee }}</span></li>
                    <li class="li"><span>{{ $L('发票税额') }}:</span><span>{{ orderInfo.taxFee }}</span></li>
                    <li class="li"><span class="font-black">{{ $L('实付金额') }}:</span><span class="font-red">{{ orderInfo.orderAmount }}</span></li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "prodList",
        props : {
            orderInfo: {
                type:Object,
                default:()=>{
                    return null;
                }
            }
        },
        data(){
          return {
              leftTime:'',
              d:'',
              h:'',
              m:'',
              s:'',
          }
        },
        created(){
            this.countTime();
        },
        methods : {
            statusFunc (status){
                let x=this.$L('买家已付款');
                switch(status){
                    case 0:
                        x = this.$L('等待买家付款');
                        break;
                    case -1:
                        x = this.$L('货到付款');
                        break;
                    case -2:
                        x = this.$L('到店付款');
                        break;
                    case -3:
                        x = this.$L('等待买家汇款');
                        break;
                }
                return x;
            },
            countTime(){
                let date = new Date();
                let now = date.getTime();
                //定义变量 d,h,m,s保存倒计时的时间
                this.orderInfo.remaining = parseInt(this.orderInfo.remaining);
                if (this.orderInfo.remaining > 0) {
                    this.d = Math.floor(this.orderInfo.remaining / (60 * 60 * 24));
                    this.h = Math.floor(this.orderInfo.remaining / (60 * 60)) - (this.d * 24);
                    this.m = Math.floor(this.orderInfo.remaining / 60) - (this.d * 24 * 60) - (this.h * 60);
                    this.s = Math.floor(this.orderInfo.remaining) - (this.d * 24 * 60 * 60) - (this.h * 60 * 60) - (this.m * 60);
                    this.h = this.h < 10 ? ('0' + this.h) : this.h;
                    this.m = this.m < 10 ? ('0' + this.m) : this.m;
                    this.s = this.s < 10 ? ('0' + this.s) : this.s;
                }
                this.orderInfo.remaining --;
                //递归每秒调用countTime方法，显示动态时间效果
                setTimeout(this.countTime, 1000);
            }
        }
    }
</script>

<style lang="scss" scoped>
    .prod-list{
        padding: 20px;
        background: #fff;
        .table {
            width: 100%;
            /deep/ .el-table__body-wrapper{
                border-left: 1px solid #ebeef5;
                border-right: 1px solid #ebeef5;
            }
            /deep/ .el-table__header-wrapper .cell{
                font-weight: 800;
            }
            .info{
                .list-main-left{
                    height: 100px;
                    float: left;
                    .img{
                        width: 100px;
                        height: 100px;
                    }
                }
                .list-main-right{
                    float: left;
                    padding-left: 15px;
                    width: calc(100% - 120px);
                    p{
                        cursor: pointer;
                        overflow:hidden;
                        display:-webkit-box;
                        text-overflow:ellipsis;
                        -webkit-line-clamp:2;
                        -webkit-box-orient:vertical;
                        white-space:normal;
                        font-size: 14px;
                    }
                }
            }
        }
        .spell{
            margin-top: 20px;
            background-color: #F2F2F2;
            padding: 20px;
            .title{
                font-size: 16px;
                .num{
                    background-color: #CC0000;
                    color: #ffffff;
                    padding: 3px;
                }
            }
            .ul{
                display: flex;
                justify-content: flex-start;
                padding: 10px 10px 0;
                .li{
                    height: 70px;
                    width: 70px;
                    margin-right: 50px;
                    background-color: dodgerblue;
                    border-radius: 50%;
                    overflow: hidden;
                    .img{
                        width: 100%;
                        height: 100%;
                    }
                }
                .text{
                    margin-top: 10px;
                    font-size: 14px;
                }
            }
            .end{
                line-height: 70px;
                font-size: 16px;
            }
        }
        .prod-bottom{
            height: 250px;
            position: relative;
            margin: 20px 0;
            .right-box{
                position: absolute;
                right: 0;
                width: 400px;
                .ul{
                    .li{
                        span{
                            display:inline-block;
                            width:50%;
                            line-height: 35px;
                            &:first-child{
                                text-align: right;
                            }
                            &:last-child{
                                text-align: left;
                                padding-left: 20px;
                            }
                        }
                        &:last-child{
                            padding: 20px 0;
                            .font-black{
                                font-weight: 700;
                            }
                            .font-red{
                                color: #FF0000;
                                font-size: 20px;
                            }
                        }
                    }
                }

            }
        }
    }
</style>